body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.clearfix {
  clear: both;
}

.container {
  width: 80%;
}
#meetup-header {
   background-color:gray ;
   padding: 10px 0;
}

#meetup-header .container {
    width:90%;
    padding:2%;
    background-color: orange;
    margin-left:auto;
    margin-right:auto;
}

#meetup-header .profile-pic {
    float: left;
}

#meetup-header img {
    width: 200px;
    height: 200px;
    background-color: yellow
}

#meetup-header .detail { 
    float: left;
    margin-left: 20px;
}

#meetup-header .detail h2 { 
    float: left;
    padding: 0px;
}

#meetup-header table {
    width: 500px;
}

#meetup-header table tr td {

}
